
import { Vue } from 'vue-class-component';
export default class KeyInputModal extends Vue {
  message: string = '';
  mounted() {
      this.loadMessage();
  }
  loadMessage() {
      const savedMessage = localStorage.getItem('api-key');
      if (savedMessage) {
          this.message = savedMessage;
      }
  }
  saveMessage() {
    localStorage.setItem('api-key', this.message);
    this.closeModal();
  }
  closeModal() {
    this.message = '';
    this.$emit('close');
  }
}
